.characterList {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
}
.character {
    border: solid black 1px;
    width: 180px;
    height: 350px;
    list-style: none;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    background-color: #ff995f;
}

a {
    text-decoration: none;
}


